<script lang="ts">import { assetMap } from '../assets';
import { MinesweeperGame } from '../minesweeper';
;
import { colorMap, tileClick$, tileMouseDown$, width$ } from '../store';
import Button from './button.svelte';
import Image from './image.svelte';
export let x;
export let y;
let value;
let revealed = false;
let mark;
let isMine = false;
let error = false;
export let disabled = false;
let onReveal;
let onMark;
let isANeighbourPressed = false;
$: pressedTile = $tileMouseDown$;
$: width = $width$;
$: if (pressedTile !== undefined) {
    if (pressedTile[1]) {
        const [ox, oy] = MinesweeperGame.fromLinear(width, pressedTile[0]);
        isANeighbourPressed = MinesweeperGame.isNeighbour(x, y, ox, oy);
    }
}
else {
    isANeighbourPressed = false;
}
export function getX() {
    return x;
}
export function getY() {
    return y;
}
export function getValue() {
    return value;
}
export function getRevealed() {
    return revealed;
}
export function getMark() {
    return mark;
}
export function getIsMine() {
    return isMine;
}
export function getError() {
    return error;
}
export function setValue(v) {
    value = v;
}
export function setRevealed(r) {
    revealed = r;
}
export function setMark(m) {
    mark = m;
}
export function setIsMine(m) {
    isMine = m;
}
export function setError(e) {
    error = e;
}
export function registerOnReveal(callback) {
    onReveal = (e) => {
        e.preventDefault();
        callback();
    };
}
export function registerOnMark(callback) {
    onMark = (e) => {
        e.preventDefault();
        callback();
    };
}
</script>

<style>
:global(.ms-tile) {
	height: 40px;
	width: 40px;
	display: block;
	box-sizing: border-box;
}

:global(.ms-tile-font) {
	font-size: 32px;
	text-align: center;
	padding: 0;
}

:global(.ms-tile-error) {
	background-color: red;
}

div {
	user-select: none;
	font-family: 'Press Start 2P', cursive;
	border-style: solid;
	border-color: #a6a6a6;
	border-width: 1px;
	text-align: center;
	font-size: 32px;
	line-height: 42px;
}

.fontpatch {
	padding-left: 4px;
}

:global(.tile-img) {
	margin-top: -1px;
	margin-left: -1px;
}
</style>

{#if revealed}
	<div
		class="ms-tile"
		class:fontpatch={!isMine && value && !error}
		class:ms-tile-error={error && isMine}
		on:click={onReveal}
		on:contextmenu={onReveal}
		on:mousedown={() => tileClick$.next([MinesweeperGame.toLinear($width$, x, y), true])}
		style="color: {colorMap[value]}; grid-row: {x + 1}; grid-column: {y + 1};">
		{#if isMine}
			<Image class="tile-img" src={assetMap.mine} alt="Mine" />
		{:else if error}
			<Image class="tile-img" src={assetMap.mineFalse} alt="False mine" />
		{:else if value}{value}{/if}
	</div>
{:else}
	<Button
		mousedown={isANeighbourPressed && !mark}
		{disabled}
		on:mousedown={() => tileClick$.next([MinesweeperGame.toLinear($width$, x, y), false])}
		class="button ms-tile ms-tile-font{error ? ' ms-tile-error' : ''}"
		style="grid-row: {x + 1}; grid-column: {y + 1};"
		aria-label="Tile {mark ? 'mark' : 'unrevealed'}"
		on:click={(e) => onReveal(e)}
		on:contextmenu={(e) => onMark(e)}>
		{#if mark === 'flag'}
			<Image class="tile-img" src={assetMap.flag} alt="Flag" />
		{:else if mark === 'questionMark'}
			<Image class="tile-img" src={assetMap.questionMark} alt="Question mark" />
		{/if}
	</Button>
{/if}
