<script lang="ts">import { Subscription } from 'rxjs';
import { onDestroy, onMount } from 'svelte';
import { MinesweeperGame } from '../minesweeper';
import { game$, GamePreset, isEnded$ } from '../store';
import Tile from './tile.svelte';
export let gamePreset;
export let game;
let s = new Subscription();
let mounted = false;
onMount(() => {
    mounted = true;
});
let tiles = [];
function tileGetter(x, y) {
    return tiles[y + 1000 * x];
}
$: {
    if (mounted) {
        game = new MinesweeperGame(gamePreset.height, gamePreset.width, gamePreset.mineCount, tileGetter);
        game$.next(game);
    }
}
onDestroy(() => {
    mounted = false;
    s.unsubscribe();
});
</script>

<style>
div {
	display: grid;
	width: fit-content;
	height: fit-content;
	margin: auto;
}
</style>

<div class={$$props.class} style={$$props.style}>
	{#each Array(gamePreset.height) as _, x}
		{#each Array(gamePreset.width) as _, y}
			<Tile bind:this={tiles[y + 1000 * x]} {x} {y} disabled={$isEnded$} />
		{/each}
	{/each}
</div>
